








import {Component, Prop, Vue, Watch} from "vue-property-decorator";

@Component
export default class TableTop extends Vue {
    @Prop({default: ''}) search?: string;
    private model = '';

    constructor() {
        super();
    }

    @Watch('model')
    updateModel(value: string) {
        this.model = value;
        this.$emit('search', value);
    }
}
